@import "../../../base/_base";

.wp-block-yohdev-header {
	padding: 30px 0;
	.content-container {
		display: flex;
		align-items: baseline;
		.yohdev-heading {
			font-family: $main-font;
			font-weight: 700;
		}

		.cool-dot {
			display: block;
			width: 15px;
			height: 15px;
			border-radius: 50%;
			margin-left: 10px;
		}
	}
}
